$("._tab").click(function() {
    let parentBlock = $(this).parents("._tabs-parent")
    let tabId = $(this).attr("data-tab")
    $(parentBlock).find("._tab").removeClass("_active")
    $(this).addClass("_active")
    $(parentBlock).find(".tab-content").removeClass("_active")
    $(parentBlock).find(`.${tabId}`).addClass("_active")
})

$(".review-card__btn").click(function () {
    let parent = $(this).parents(".review-card")
    let city = $(parent).find(".review-card__city").html().trim()
    let name = $(parent).find(".review-card__name").html().trim()
    let text = $(parent).find(".review-card__text").html().trim()

    console.log(city)
    console.log(name)
    console.log(text)
    $("#reviewModal .review-card__city").html(city)
    $("#reviewModal .review-card__name").html(name)
    $("#reviewModal .review-card__text").html(text)
    openModal("reviewModal")
})


$("[data-copy]").click(function () {
    let text = $(this).attr("data-copy")
    let $tmp = $("<textarea>");
    $("body").append($tmp);
    $tmp.val(text).select();
    document.execCommand("copy");
    $tmp.remove();
})

$(".pass-toggle").click(function () {
    if($(this).hasClass("_active")){
        $(this).removeClass("_active")
        $(this).siblings("input").attr("type","password")
    } else{
        $(this).addClass("_active")
        $(this).siblings("input").attr("type","text")
    }
})


$(".account-nav__mob").click(function () {
    $(".account-nav").toggleClass("_active")
    $(".account-nav__links").slideToggle()
})

$(".order-card__show").click(function () {
    $(this).parents(".order-card").toggleClass("_active")
    $(this).siblings(".order-card__hidden").slideToggle()
})
